import React, { useEffect, useRef, useState } from 'react';
import ACTIONS from '../Actions.js';

const Chat = ({ socketRef, roomId, username }) => {
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState('');
    const messagesEndRef = useRef(null);

    useEffect(() => {
        if (!socketRef.current) return;
        const handleMessage = ({ username: sender, message }) => {
            console.log('Client received:', sender, message);
            setMessages(prev => [...prev, { sender, message }]);
        };
        socketRef.current.on(ACTIONS.CHAT_MESSAGE, handleMessage);
        return () => {
            socketRef.current.off(ACTIONS.CHAT_MESSAGE, handleMessage);
        };
    }, [socketRef]);

    useEffect(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, [messages]);

    const sendMessage = (e) => {
        e.preventDefault();
        if (!input.trim()) return;
        const msg = input;
        console.log('Client sending:', username, msg, roomId);
        socketRef.current.emit(ACTIONS.CHAT_MESSAGE, {
            roomId,
            username,
            message: msg,
        });
        setInput('');
    };

    return (
        <div className="chatSidebar">
            <div className="chatHeader">Live Chat</div>
            <div className="chatMessages">
                {messages.map((msg, idx) => (
                    <div key={idx} className={msg.sender === username ? 'myMessage' : 'otherMessage'}>
                        <span className="chatUsername">{msg.sender}:</span> {msg.message}
                    </div>
                ))}
                <div ref={messagesEndRef} />
            </div>
            <form className="chatInputForm" onSubmit={sendMessage}>
                <input
                    className="chatInput"
                    type="text"
                    placeholder="Type a message..."
                    value={input}
                    onChange={e => setInput(e.target.value)}
                    onKeyDown={e => { if (e.key === 'Enter' && !e.shiftKey) sendMessage(e); }}
                />
                <button className="chatSendBtn" type="submit">Send</button>
            </form>
        </div>
    );
};

export default Chat; 